import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Login from '@/pages/login/login'
import Home from '@/pages/home/home'
import Lottery from '@/pages/lottery/lottery'
// import Crpopper from '@/pages/crpopper/crpopper'
import Axios from '@/pages/axios/axios'
import Compress from '@/pages/compress/compress'
import Crpopper from '@/pages/crpopper/crpopper'
import UploadExcel from '@/pages/uploadExcel/uploadExcel'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/home/:name',
      name: 'Home',
      component: Home
    },
    {
      path: '/hello',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/lottery',
      name: 'Lottery',
      component: Lottery
    },
    {
      path: '/crpopper',
      name: 'Crpopper',
      component: Crpopper
    },
    {
      path: '/axios',
      name: 'Axios',
      component: Axios
    },
    {
      path: '/compress',
      name: 'Compress',
      component: Compress
    },
    {
      path: '/uploadExcel',
      name: 'UploadExcel',
      component: UploadExcel
    }
  ]
})



