<template>
  <div>
      <div>{{uploadExcel}}</div>
      <input type="file" value="上传文件" @change="uploadFile($event)">
  </div>
</template>
<script>
    export default{
        data(){
            return{
                uploadExcel:'上传文件'
            }
        },
        mounted(){},
        methods:{
            uploadFile(e){
                console.log(e)
            }
        }
    }
</script>



